import styled from 'styled-components';

const TodoListContentFilter = styled.div`
  text-align:center;
  margin:10px;
`
const ContentFunctions = styled.div`
  display:flex;
  justify-content:space-between;
  padding:5px;
`
const Status = styled.div`
  color:#8C495E;
  border:1px solid;
  font-size:10px;
  border-radius:3px;
  cursor: pointer;
  padding:3px 2px;
`
const Content = styled.div`
  color:#8C495E;
  ${props => props.isDone && `
    text-decoration:line-through;
  `}
`
/*
const Edit = styled.button`
  color:#8C495E;
  background:#D1D1C2;
  margin-left:20px;
  cursor:pointer;
  outline: none;
`
*/
const Delete = styled.button`
  color:#8C495E;
  background:rgba(255,255,255,0.5);
  margin-left:20px;
  cursor:pointer;
  outline: none;
  box-shadow:2px 2px ;
  border:none;
  :active {
    transform: translateY(2px);
    box-shadow:1px 1px ;
  }
`


function TodoListContentFunction({todo,handleDeleteTodo,handleToggleDone}) {
  return (
    <TodoListContentFilter >
      <ContentFunctions>
        <Status onClick={ () => {handleToggleDone(todo.id) }} >{todo.isDone?'Done':'Todo'}</Status>
        <Content isDone={todo.isDone}>{todo.content}</Content>
        <div>
          <Delete onClick={ () => {handleDeleteTodo(todo.id)} }>Delete</Delete>
        </div>
      </ContentFunctions>
    </TodoListContentFilter>
  );
}

export default TodoListContentFunction;