import styled from 'styled-components';

const FilterWrapper = styled.div`
  margin:0px auto;
`
const FilterFunctions = styled.div`
  margin:30px;
`
const FilterButton = styled.button`
  border:none;
  outline:none;
  box-shadow:2px 2px;
  background:rgba(255,255,255,0.5);
  color:#9c6375;
  margin:15px 15px 30px 15px;

  cursor:pointer;
  :active {
    box-shadow:1px 1px;
  }
  
  
`

function TodoListFilterFunction({handleFilterAll,handleFilterNotYet,handleFilterDone,handleCleanUp}) {
  return (
    <FilterWrapper>
      <FilterFunctions>
        <FilterButton onClick={handleFilterAll}>All</FilterButton>
        <FilterButton onClick={handleFilterNotYet}>To do</FilterButton>
        <FilterButton onClick={handleFilterDone}>Done</FilterButton>
        <FilterButton onClick={handleCleanUp}>Clear</FilterButton>
      </FilterFunctions>
    </FilterWrapper>
  );
}

export default TodoListFilterFunction;
