import styled from 'styled-components';
import React from 'react';

const TodoListInputWrapper = styled.div`
  margin-bottom:30px;
  
`

const InputFunctions = styled.input`
  width:300px;
  padding:5px;
  border:0px;
  outline:none;
`
const InputButton = styled.button`
  color:#8C495E;
  background:rgba(255,255,255,0.5);
  margin-left:20px;
  border:none;
  box-shadow:2px 2px ;
  cursor:pointer;
  outline: none;
  :active {
    color:black;
    transform: translateY(2px);
    box-shadow:1px 1px ;
  }
`


function TodoListInputFunction({value,handleButtonClick,handleInputChange}) {

  return (
    <TodoListInputWrapper >
      <InputFunctions placeholder="Ha! I can help you memorize something" value={value} onChange={handleInputChange}/>
      <InputButton onClick={handleButtonClick} > Add! </InputButton>
    </TodoListInputWrapper>
  );
}

export default TodoListInputFunction;