import styled from 'styled-components';
import React, { useEffect } from 'react';
import TodoListInputFunction from './TodoListInputFunction.js';
import TodoListContentFunction from './TodoListContentFunction.js';
import TodoListFilterFunction from './TodoListFilterFunction.js'
import background from '../background/background.jpg';

const TodoListWrapper = styled.div`
  margin:150px auto;
  background: rgba(255,255,255,0.5);
  width:450px;
  text-align:center;
  box-shadow:6px 8px 6px rgba(0, 0, 0, 0.4);
  border-radius:2px;
`
const TodoListTitle = styled.div`
  color:#8C495E;
  padding:25px;
  font-size:30px;
  font-family:monospace;
`
const Background = styled.div`
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: -999;
  background-repeat: no-repeat;
  background-position: center center;
  background-image: url("${background}");

`

let id = 3

function App() {

  const [value,setValue] = React.useState("")
  const [todos, setTodos] = React.useState([
    {id:2, content:'Climb the tree', isDone:true},
    {id:1, content:'Wandering around the street', isDone:false}
  ])

  const [filter, setFilter] = React.useState('all')
  
  const handleInputChange = (e) => {
    let inputValue = e.target.value
    setValue(inputValue)
  }

  const handleButtonClick = () => {
    if(value !== "") {
      setTodos([
        {
          id:id,
          content:value,
          isDone:false
        },
        ...todos])
        id++
    }
    setValue("")
  }
  
  const handleDeleteTodo = id => {
    setTodos(todos.filter(todo => todo.id !== id))
  }

  const handleToggleDone = id => {
    setTodos(
      todos.map(todo => {
      if(todo.id !== id) return todo
      return {
        ...todo,
        isDone:!todo.isDone
      }
    }))
  }

  const handleFilterAll = () => {
    setFilter('all')
  }

  const handleFilterNotYet = () => {
    setFilter('notYet')
  }

  const handleFilterDone = () => {
    setFilter('completed')
  }

  const handleCleanUp = () => {
    setTodos([])
  }

  return (
    <div className="App">
      <TodoListWrapper>
        <Background/>
        <TodoListTitle>I am TodoList</TodoListTitle>
        <TodoListInputFunction handleButtonClick={handleButtonClick} handleInputChange={handleInputChange} value={value}/>
        {
          todos.filter((todo) => {
            if (filter === 'all') {
              return true
            }
            if (filter === 'completed') {
               return todo.isDone 
            }
            else if (filter === 'notYet') {
               return !todo.isDone 
            }
          })
          .map(todo => <TodoListContentFunction key={todo.id} todo={todo} handleDeleteTodo={handleDeleteTodo} handleToggleDone={handleToggleDone}/> )
        }
        <TodoListFilterFunction handleFilterAll={handleFilterAll} handleFilterNotYet={handleFilterNotYet} handleFilterDone={handleFilterDone} handleCleanUp={handleCleanUp} />
      </TodoListWrapper>
    </div>
  );
}

export default App;
